.countryList {
  text-align: center;
}
.countryList select {
  max-width: 90vw;
  font-size: 1.1rem;
}
.countryList option {
  word-wrap: break-word;
}
.countryList a {
  margin: 1em;
}
.h1{
  font-family: 'Luckiest Guy', cursive;
  color: crimson;
  
}
*{
  padding: 0;
  margin: 0;
  list-style: none;
  text-decoration: none;
  
}

.sidebar {
  position: fixed;
  left: -300px;
  width: 300px;
  height: 100%;
  background: #042331;
  transition: all .5s ease;
  
}
.sidebar header {
  font-size: 22px;
  color: white;
  line-height: 70px;
  /* text-align: center; */
  background: #063146;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-top: -25px;
  width: 300px;
  padding-left: 30px;
}
.sidebar ul a{
  display: block;
  height: 100%;
  width: 100%;
  line-height: 65px;
  font-size: 20px;
  color: white;
  padding-left: 30px;
  box-sizing: border-box;
  border-bottom: 1px solid black;
  border-top: 1px solid rgba(255,255,255,.1);
  transition: .5s;
}
ul li:hover a{
  padding-left: 50px;
}
.sidebar ul a i{
  margin-right: 16px;
}
#check{
  display: none;
}
label #btn,label #cancel{
  position: absolute;
 
  border-radius: 3px;
  cursor: pointer;
}
label #btn{
  left: 40px;
  top: 25px;
  font-size: 30px;
  color: #042331;
  padding: 6px 12px;
  background:white;
  transition: all .5s;
}
label #cancel{
  z-index: 1111;
  left: -195px;
  background:#0a5275;
  top: 17px;
  font-size: 30px;
  color: #042331;
  padding: 4px 9px;
  transition: all .5s ease;
  margin-left : 40px;
  
}
#check:checked ~ .sidebar{
  left: 0;
}
#check:checked ~ label #btn{
  left: 250px;
  opacity: 0;
  pointer-events: none;
}
#check:checked ~ label #cancel{
  left: 205px;
}
#check:checked ~ section{
    margin-left: 280px;
  }
  section{

    background-position: center;
    background-size: cover;
    height: 100vh;
    transition: all .5s;
  }
  header {
    padding: 20px;
}
select.countryList:focus {
    outline: none !important;
}
.hdrCls:hover {
    border: 2px solid rgba(255, 255, 255, 0.986) !important;
  }

.hdrCls {
    background-color: #063146;
    color: #ffffff;
    font-weight: 500;
    font-size: x-large;
}

.ftrCls {
    background-color: #063146;
    color: #ffffff;
    font-weight: 500;
    font-size: larger;
}

.cellCls {
     background-color: #ffffff;
     font-weight: 400;
     font-size: large;
}


.image-container:hover{
    transform: scale(1.1);
  transition-duration: 1000ms;
}

.card-list{
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:25px;
    padding: 10px;
    
}
.card-container{
    display: flex;
    flex-direction:column;
    background-color: white;
    border: 1px solid goldenrod;
    border-radius: 5px;
    padding: 0px;
    cursor: pointer;
    -moz-osx-font-smoothing:grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: translatez(0);
    transition: transform 0.5 ease-out;
    
}
.card-container:hover{
    transform: scale(1.01);
    transition-duration: 500ms;
}

a.btn.btn-secondary {
    position: absolute;
    bottom: 5px;
    left: 50px;
}
